@import '../../utils/theme';

:root {
  --font-stack: Helvetica, Arial, sans-serif;
}

@mixin error-message-base {
  font-family: var(--font-stack);
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  margin-top: 4px;
}

.errorMessageLight {
  @include error-message-base;
  color: $light-input-caption-color-error;
}

.errorMessageDark {
  @include error-message-base;
  color: $dark-input-caption-color-error;
}